"use client"

import * as React from "react"
import { DashboardLayout } from "@/components/dashboard/layout"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Switch } from "@/components/ui/switch"
import { GripVertical, Plus, Trash2, Save } from "lucide-react"
import { Spinner } from "@/components/ui/spinner"
import axiosClient from "@/lib/axiosClient"

type Field = {
  key: string
  label: string
  type: "text" | "textarea" | "number" | "boolean" | "url" | "email" | "date" | "file" | "image"
  required?: boolean
  analytics?: boolean
  tempId?: string // for new unsaved fields
}

type Section = {
  id?: number
  key: string
  name: string
  group: string
  sub_group: string | null
  description: string
  schema: {
    component: string
    fields: Field[]
  }
  is_active: boolean
  sort_order: number
  tempId?: string // for new unsaved sections
}

const FIELD_TYPES = [
  "text",
  "textarea",
  "number",
  "boolean",
  "url",
  "email",
  "date",
  "file",
  "image",
] as const

const GROUPS = ["content", "media", "social", "conversion"] as const

export default function GlobalLandingPageSections() {
  const [sections, setSections] = React.useState<Section[]>([])
  const [loading, setLoading] = React.useState(true)
  const [saving, setSaving] = React.useState(false)

  // Fetch all sections
  React.useEffect(() => {
    async function loadSections() {
      try {
        const res = await axiosClient.get("/admin/landing-page/sections")
        if (res.data.success) {
          setSections(res.data.data)
        }
      } catch (err) {
        console.error("Failed to load sections:", err)
      } finally {
        setLoading(false)
      }
    }
    loadSections()
  }, [])

  // Update a top-level section property
  const updateSection = (id: number | string, key: keyof Section, value: any) => {
    setSections((prev) =>
      prev.map((s) => (s.id === id || s.tempId === id ? { ...s, [key]: value } : s))
    )
  }

  // Update schema.component
  const updateComponent = (id: number | string, component: string) => {
    setSections((prev) =>
      prev.map((s) =>
        s.id === id || s.tempId === id
          ? { ...s, schema: { ...s.schema, component } }
          : s
      )
    )
  }

  // Update a field inside schema.fields
  const updateField = (
    sectionId: number | string,
    fieldTempId: string,
    fieldKey: keyof Field,
    value: any
  ) => {
    setSections((prev) =>
      prev.map((s) => {
        if (s.id === sectionId || s.tempId === sectionId) {
          return {
            ...s,
            schema: {
              ...s.schema,
              fields: s.schema.fields.map((f) =>
                f.tempId === fieldTempId ? { ...f, [fieldKey]: value } : f
              ),
            },
          }
        }
        return s
      })
    )
  }

  // Add a new field to a section
  const addField = (sectionId: number | string) => {
    const newField: Field = {
      key: "",
      label: "",
      type: "text",
      required: false,
      analytics: false,
      tempId: Date.now().toString(),
    }
    setSections((prev) =>
      prev.map((s) =>
        s.id === sectionId || s.tempId === sectionId
          ? { ...s, schema: { ...s.schema, fields: [...s.schema.fields, newField] } }
          : s
      )
    )
  }

  // Remove a field
  const removeField = (sectionId: number | string, fieldTempId: string) => {
    setSections((prev) =>
      prev.map((s) =>
        s.id === sectionId || s.tempId === sectionId
          ? {
              ...s,
              schema: {
                ...s.schema,
                fields: s.schema.fields.filter((f) => f.tempId !== fieldTempId),
              },
            }
          : s
      )
    )
  }

  // Add a new section
  const addSection = () => {
    const newSection: Section = {
      key: "",
      name: "",
      group: "content",
      sub_group: null,
      description: "",
      schema: {
        component: "",
        fields: [],
      },
      is_active: true,
      sort_order: sections.length,
      tempId: Date.now().toString(),
    }
    setSections([...sections, newSection])
  }

  // Remove a section (only if it has no id yet or call DELETE if it has)
  const removeSection = async (section: Section) => {
    if (section.id) {
      try {
        await axiosClient.delete(`/admin/landing-page/sections/${section.id}`)
        setSections((prev) => prev.filter((s) => s.id !== section.id))
      } catch (err) {
        console.error("Delete failed:", err)
      }
    } else {
      setSections((prev) => prev.filter((s) => s.tempId !== section.tempId))
    }
  }

  // Save all changes (create new + update existing)
  const saveAll = async () => {
    setSaving(true)
    try {
      // Create new sections
      const newSections = sections.filter((s) => !s.id)
      for (const sec of newSections) {
        await axiosClient.post("/admin/landing-page/sections", {
          key: sec.key,
          name: sec.name,
          group: sec.group,
          sub_group: sec.sub_group,
          description: sec.description,
          schema: sec.schema,
          is_active: sec.is_active,
          sort_order: sec.sort_order,
        })
      }

      // Update existing sections
      const existingSections = sections.filter((s): s is Section & { id: number } => !!s.id)
      for (const sec of existingSections) {
        await axiosClient.put(`/admin/landing-page/sections/${sec.id}`, {
          key: sec.key,
          name: sec.name,
          group: sec.group,
          sub_group: sec.sub_group,
          description: sec.description,
          schema: sec.schema,
          is_active: sec.is_active,
          sort_order: sec.sort_order,
        })
      }

      alert("All changes saved successfully!")
      // Optionally refetch to get fresh data
      const res = await axiosClient.get("/admin/landing-page/sections")
      if (res.data.success) setSections(res.data.data)
    } catch (err: any) {
      console.error(err)
      alert("Save failed: " + (err.response?.data?.message || err.message))
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <DashboardLayout>
        <div className="p-10 flex items-center justify-center">
          <Spinner className="size-8" />
        </div>
      </DashboardLayout>
    )
  }

  return (
    <DashboardLayout>
      <div className="max-w-6xl mx-auto space-y-8 py-8">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-foreground">Global Landing Page Sections</h1>
            <p className="text-sm text-muted-foreground mt-1">
              Manage reusable section templates and their field schemas
            </p>
          </div>

          <Button onClick={saveAll} disabled={saving} className="gap-2">
            {saving ? <Spinner className="size-4" /> : <Save className="h-4 w-4" />}
            {saving ? "Saving..." : "Save All Changes"}
          </Button>
        </div>

        {/* Sections List */}
        <div className="space-y-6">
          {sections.map((section) => (
            <Card key={section.id ?? section.tempId} className="overflow-hidden">
              <CardHeader className="border-b bg-muted/30">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <GripVertical className="h-5 w-5 text-muted-foreground cursor-grab" />
                    <div>
                      <Input
                        value={section.name || "(Untitled)"}
                        onChange={(e) => updateSection(section.id ?? section.tempId!, "name", e.target.value)}
                        className="text-lg font-semibold h-9 w-64"
                        placeholder="Section Name"
                      />
                      <p className="text-xs text-muted-foreground mt-1">
                        Key: <code className="bg-muted px-1 rounded">{section.key || "(set key)"}</code>
                      </p>
                    </div>
                  </div>

                  <div className="flex items-center gap-4">
                    <div className="flex items-center gap-2">
                      <Switch
                        checked={section.is_active}
                        onCheckedChange={(v) => updateSection(section.id ?? section.tempId!, "is_active", v)}
                      />
                      <Label className="text-sm">Active</Label>
                    </div>
                    <Button
                      variant="ghost"
                      size="icon"
                      onClick={() => removeSection(section)}
                    >
                      <Trash2 className="h-4 w-4 text-destructive" />
                    </Button>
                  </div>
                </div>
              </CardHeader>

              <CardContent className="pt-6 space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <div className="space-y-2">
                    <Label>Key (unique identifier)</Label>
                    <Input
                      value={section.key}
                      onChange={(e) => updateSection(section.id ?? section.tempId!, "key", e.target.value)}
                      placeholder="hero"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label>Group</Label>
                    <Select
                      value={section.group}
                      onValueChange={(v) => updateSection(section.id ?? section.tempId!, "group", v)}
                    >
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        {GROUPS.map((g) => (
                          <SelectItem key={g} value={g}>
                            {g.charAt(0).toUpperCase() + g.slice(1)}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div className="space-y-2">
                    <Label>Sub Group (optional)</Label>
                    <Input
                      value={section.sub_group ?? ""}
                      onChange={(e) => updateSection(section.id ?? section.tempId!, "sub_group", e.target.value || null)}
                      placeholder="e.g., carousel"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label>Component Name</Label>
                    <Input
                      value={section.schema.component}
                      onChange={(e) => updateComponent(section.id ?? section.tempId!, e.target.value)}
                      placeholder="HeroSection"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label>Sort Order</Label>
                    <Input
                      type="number"
                      value={section.sort_order}
                      onChange={(e) => updateSection(section.id ?? section.tempId!, "sort_order", Number(e.target.value))}
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <Label>Description</Label>
                  <Textarea
                    value={section.description}
                    onChange={(e) => updateSection(section.id ?? section.tempId!, "description", e.target.value)}
                    placeholder="Brief description of this section..."
                    rows={2}
                  />
                </div>

                {/* Fields Schema */}
                <div className="space-y-4 pt-4 border-t">
                  <div className="flex items-center justify-between">
                    <h4 className="font-medium">Schema Fields</h4>
                    <Button size="sm" variant="outline" onClick={() => addField(section.id ?? section.tempId!)}>
                      <Plus className="h-4 w-4 mr-2" /> Add Field
                    </Button>
                  </div>

                  {section.schema.fields.length === 0 && (
                    <p className="text-sm text-muted-foreground text-center py-4">No fields defined yet.</p>
                  )}

                  {section.schema.fields.map((field) => (
                    <div
                      key={field.tempId}
                      className="flex items-end gap-4 rounded-lg border p-4 bg-background"
                    >
                      <div className="flex-1 grid grid-cols-1 sm:grid-cols-4 gap-3">
                        <div className="space-y-2">
                          <Label className="text-xs">Key</Label>
                          <Input
                            value={field.key}
                            onChange={(e) => updateField(section.id ?? section.tempId!, field.tempId!, "key", e.target.value)}
                            placeholder="heading"
                          />
                        </div>

                        <div className="space-y-2">
                          <Label className="text-xs">Label</Label>
                          <Input
                            value={field.label}
                            onChange={(e) => updateField(section.id ?? section.tempId!, field.tempId!, "label", e.target.value)}
                            placeholder="Heading"
                          />
                        </div>

                        <div className="space-y-2">
                          <Label className="text-xs">Type</Label>
                          <Select
                            value={field.type}
                            onValueChange={(v) => updateField(section.id ?? section.tempId!, field.tempId!, "type", v)}
                          >
                            <SelectTrigger>
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              {FIELD_TYPES.map((t) => (
                                <SelectItem key={t} value={t}>
                                  {t}
                                </SelectItem>
                              ))}
                            </SelectContent>
                          </Select>
                        </div>

                        <div className="flex items-center gap-4">
                          <div className="flex items-center gap-2">
                            <Switch
                              checked={field.required ?? false}
                              onCheckedChange={(v) => updateField(section.id ?? section.tempId!, field.tempId!, "required", v)}
                            />
                            <Label className="text-xs">Required</Label>
                          </div>

                          <div className="flex items-center gap-2">
                            <Switch
                              checked={field.analytics ?? false}
                              onCheckedChange={(v) => updateField(section.id ?? section.tempId!, field.tempId!, "analytics", v)}
                            />
                            <Label className="text-xs">Analytics</Label>
                          </div>
                        </div>
                      </div>

                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => removeField(section.id ?? section.tempId!, field.tempId!)}
                      >
                        <Trash2 className="h-4 w-4 text-destructive" />
                      </Button>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          ))}

          {/* Add New Section Button */}
          <Button variant="outline" className="w-full h-20 text-lg gap-3" onClick={addSection}>
            <Plus className="h-6 w-6" />
            Add New Global Section
          </Button>
        </div>
      </div>
    </DashboardLayout>
  )
}